import {createElement} from '../render.js';

const TEMPLATE = '<ul class="trip-events__list"></ul>';

export default class ListView {
  getTemplate() {
    return TEMPLATE;
  }

  getElement() {
    if (!this.element) {
      this.element = createElement(this.getTemplate());
    }
    return this.element;
  }

  removeElement() {
    this.element = null;
  }
}
